<template>
  <div class="modal fade" id="productModal"
  tabindex="-1" role="dialog" aria-labelledby="productModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title " id="exampleModalLabel">
            <span></span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="form-group">
                <label for="imageUrl"></label>
                <input id="imageUrl" v-model="product.imageUrl[0]"
                class="form-control" type="text" placeholder="" >
              </div>
              <div class="form-group">
                <label for="customFile">
                   
                </label>
                <input id="customFile"
                ref="file"
                type="file"
                class="form-control"
                @change="uploadFile">
                <img class="img-fluid mt-5" :src="product.imageUrl[0]" alt="">
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="title"></label>
                  <input id="title" v-model="product.title"
                  class="form-control" type="text" placeholder="" required>
                </div>
                <div class="form-group col-md-6">
                  <label for="inventory"></label>
                  <input id="inventory" v-model="product.options.inventory"
                  class="form-control" type="text" placeholder="" required>
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input type="text" v-model="product.category"
                  class="form-control" id="category" placeholder="" required>
                </div>
                <div class="form-group col-md-6">
                  <label for="unit"></label>
                  <input type="text" v-model="product.unit"
                  class="form-control" id="unit" placeholder="">
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input type="text" v-model="product.origin_price"
                  class="form-control" id="origin_price" placeholder="">
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input type="text" v-model="product.price"
                  class="form-control" id="price" placeholder="">
                </div>
              </div>
              <hr>
              <div class="form-group">
                <label for="description"></label>
                <vue-editor id='description' v-model="product.description" required/>
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea id='content' v-model="product.content"
                class="form-control" placeholder="" required></textarea>
              </div>
              <div class="form-check">
                <input class="form-check-input"
                v-model="product.enabled" type="checkbox" id="enabled">
                <label  class="form-check-label" for="enabled"></label>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click="updateProduct">
            <b-spinner small type='grow' v-if='status'></b-spinner>
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* global $ */
import { VueEditor } from 'vue2-editor';

export default {
  props: ['tempProduct'],
  components: {
    VueEditor,
  },
  data() {
    return {
      product: {
        imageUrl: [],
        options: {
          inventory: 0,
        },
      },
      status: false,
    };
  },
  methods: {
    updateProduct() {
      let api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product`;
      let httpMethod = 'post';

      if (this.product.id) {
        api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${this.product.id}`;
        httpMethod = 'patch';
      }
      this.status = true;
      this.$http[httpMethod](api, this.product)
        .then(() => {
          this.$emit('update');
          this.status = false;
          $('#productModal').modal('hide');
          this.product = {};
        }).catch((err) => {
          this.$swal(
            '',
            err.response.data.errors[0],
            'error',
          );
        });
    },
    getProduct(id) {
      if (id) {
        const api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
        const loader = this.$loading.show();
        this.$http.get(api)
          .then((res) => {
            this.product = res.data.data;
            $('#productModal').modal('show');
            loader.hide();
          });
      } else {
        this.product = {
          imageUrl: [],
          options: {
            inventory: 0,
          },
        };
      }
    },
    uploadFile() {
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/storage`;

      const uploadedFile = this.$refs.file.files[0];
      // Form Data
      const formData = new FormData();
      formData.append('file', uploadedFile);

      const loader = this.$loading.show();
      this.$http.post(url, formData, {
        // form-data
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((res) => {
        this.product.imageUrl.push(res.data.data.path);
        loader.hide();
      }).catch((err) => {
        this.$swal(
          '',
          err.response.data.errors[0],
          'error',
        );
      });
    },
  },
  created() {
    this.product = this.tempProduct;
  },
};
</script>

<style lang="sass">
@import "~vue2-editor/dist/vue2-editor.css"

