<template>
  <div>
    <div class="text-right mt-4">
      <button type="button" class="btn btn-primary" @click="openModal('new')"></button>
    </div>
    <table class="table mt-4">
      <thead class="thead-dark">
          <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th width='120'></th>
          </tr>
      </thead>
      <tbody>
          <tr v-for='product in products' :key='product.id'>
            <td width='100'>
              <img :src="product.imageUrl[0]" class="img-fluid" alt="">
            </td>
            <td>{{ product.category }}</td>
            <td>{{ product.title }}</td>
            <td>{{ product.options.inventory }}</td>
            <td>{{ product.origin_price }}</td>
            <td>{{ product.price }}</td>
            <td>
              <span v-if='product.enabled' class="text-success"></span>
              <span v-else></span>
            </td>
            <td>
              <div class="btn-group">
                <button type="button" class="btn btn-outline-primary btn-sm"
                @click="openModal('edit', product)"></button>
                <button type="button" class="btn btn-outline-danger btn-sm"
                @click="openModal('delete', product)"></button>
              </div>
            </td>
          </tr>
      </tbody>
    </table>

    <Pagination @emit-pages='getProducts' :pages='pagination'/>

    <ProductModal :temp-product='tempProduct' ref='productModal' @update="getProducts"/>

    <DeleteModal :temp-product='tempProduct' @update='getProducts'/>

  </div>
</template>

<script>
/* global $ */
import ProductModal from '@/components/backend/ProductModal.vue';
import DeleteModal from '@/components/backend/DeleteModal.vue';
import Pagination from '@/components/backend/Pagination.vue';

export default {
  components: {
    ProductModal,
    DeleteModal,
    Pagination,
  },
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {
        imageUrl: [],
        options: {
          inventory: 0,
        },
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      const apiUrl = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/products`;

      const params = {
        page,
        paged: 10,
      };
      const loader = this.$loading.show();
      this.$http.get(apiUrl, { params })
        .then((res) => {
          this.products = res.data.data; // 
          this.pagination = res.data.meta.pagination; // 
          loader.hide();
        })
        .catch((err) => {
          this.$swal(
            '',
            err.response.data.errors[0],
            'error',
          );
        });
    },
    openModal(status, item) {
      switch (status) {
        case 'new':
          this.tempProduct = {
            imageUrl: [],
            options: {
              inventory: 0,
            },
          };

          //  input 
          $('#customFile').val('');
          $('#productModal').modal('show');
          this.$refs.productModal.getProduct();
          break;

        case 'edit':
          $('#customFile').val('');
          this.tempProduct = { ...item };
          this.$refs.productModal.getProduct(this.tempProduct.id);
          break;

        case 'delete':
          this.tempProduct = { ...item };
          $('#deleteModal').modal('show');
          break;
        default:
          break;
      }
    },
  },
  created() {
    this.getProducts();
  },
};
</script>
